.aboutMe {
  background: var(--primary-gray-color);
}

.aboutMe-light {
  background: var(--third-white-color);
}

.content {
  position: relative;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  row-gap: 80px;
  padding: 115px 10px 159px;
  overflow: hidden;
}

.box-left-img {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  right: -40px;
  width: 330px;

  @media screen and (max-width: 424px) {
    right: 0px;
  }
}

.box-left-img::before {
  content: "";
  position: absolute;
  top: 0;
  left: -160px;
  width: 792px;
  height: 792px;
  box-sizing: border-box;
  border-radius: 50%;
  border: 89px solid var(--primary-white-color);
  box-shadow: 0px 4px 70px 0px rgba(255, 255, 255, 0.25);
}

.left-top-img {
  position: relative;
  display: flex;
  top: -47px;
  right: 0;
  height: 378px;
  width: 330px;
  padding: 28px;
  border-radius: 14px;
  z-index: 5;
  background: var(--secondary-black-color);
}

.left-top-img::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 272px;
  height: 320px;
  border: 6px solid var(--secondary-gray-color);
  transform: translate(-50%, -50%);
}

.left-top-img-light {
  background: var(--primary-white-color);
}

.left-bottom-img {
  position: relative;
  display: flex;
  right: 0;
  bottom: -45px;
  width: 307px;
  height: 339px;
  padding: 28px;
  border-radius: 14px;
  z-index: 5;
  background: var(--secondary-black-color);
}

.left-bottom-img::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 250px;
  height: 281px;
  border: 6px solid var(--secondary-gray-color);
  transform: translate(-50%, -50%);
}

.left-bottom-img-light {
  background: var(--primary-white-color);
}

.box-desc {
  position: relative;
  max-width: 496px;
  color: var(--primary-white-color);
}

.box-desc::before {
  content: "";
  position: absolute;
  right: -210px;
  bottom: -100px;
  width: 486px;
  height: 486px;
  box-sizing: border-box;
  border-radius: 486px;
  border: 55px solid var(--primary-blue-color);
  box-shadow: 0px 4px 70px 0px rgba(0, 193, 236, 0.25);
}

.box-desc-light {
  color: var(--primary-black-color);
}

.title {
  position: absolute;
  left: 63px;
  top: -27px;
  font-size: 48px;
  font-weight: 400;
  line-height: 1.32;
  z-index: 5;
}

.text {
  position: relative;
  padding: 63px;
  border-radius: 14px;
  font-size: 20px;
  line-height: 1.34;
  letter-spacing: 0.2px;
  z-index: 2;
  background: var(--secondary-black-color);
}

.text-light {
  background: var(--primary-white-color);
}

.box-right-img {
  position: relative;
  display: flex;
  align-items: center;
  left: -40px;

  @media screen and (max-width: 424px) {
    left: 0px;
  }
}

.box-right-img::before {
  content: "";
  position: absolute;
  top: -20px;
  left: -100px;
  width: 356px;
  height: 356px;
  box-sizing: border-box;
  border-radius: 356px;
  border: 34px solid var(--primary-yellow-color);
  box-shadow: 0px 4px 70px 0px rgba(226, 162, 0, 0.25);
}

.right-img {
  position: relative;
  display: flex;
  padding: 28px;
  width: 344px;
  height: 485px;
  border-radius: 14px;
  z-index: 5;
  background: var(--secondary-black-color);
}

.right-img::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 287px;
  height: 427px;
  border: 6px solid var(--secondary-gray-color);
  transform: translate(-50%, -50%);
}

.right-img-light {
  background: var(--primary-white-color);
}
